.sidebar{
  &.sidebar-filter{
    padding: 0 1em;

    h3{
      margin: 0 0 0.5em;
      padding: 0 0 0.25em;
      font-size: 1.25em;
      text-transform: uppercase;
      border-bottom: 1px solid #fbdbbd;
      position: relative;
      &:after{
        width: 100%;
        height: 1px;
        background: #ff7e00;
        position: absolute;
        left: 0;
        bottom: -2px;
      }
    }
    .w-filter{
      margin: 1.5em 0;
      ul{
        li{
          display: block;
          margin: 0.25em 0;
          label{
            vertical-align: top;
            position: relative;
            padding: 0 0 0 24px;
            input[type=checkbox]{
              display: none;
              &:checked + .filtros-check{            
                background: #ff7e00; 
                border: 1px solid #ff7e00;             
              }
            }
            .filtros-check{
              position: absolute;
              top: 0;
              text-align: center;
              left: 0;
              display: block;
              width: 18px;
              height: 18px;
              border: 1px solid #e1e1e1;
              border-radius: 2px;
              i{
                font-size: 10px;
                position: absolute;
                color: #fff;
                top: 2px;
                left: 3px;
              }
            }
          }
        }
        &.colors{
          li{
            display: inline-block;
            margin: 0 5px 0 0;
            &:last-child{
              margin: 0;
            }
            a{
              span{
                display: block;
                width: 20px;
                height: 20px;
                background: #ccc;
              }
              &.red{
                span{
                  background: red;
                }
              }
              &.azul{
                span{
                  background: blue;
                }
              }
              &.rosado{
                span{
                  background: pink;
                }
              }
              &.verde{
                span{
                  background: green;
                }
              }
              &.amarillo{
                span{
                  background: yellow;
                }
              }
            }
          }
        }
      }
    }
  }
}