
html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: $f-main;
  background: #FFFBE1;
  font-size: 16px;
}



h1, h2, h3, h4, h5, h6 {
  font-family: $f-main;
  margin: 0;
}



a{
  //transition: all 0.1s ease-in-out;
}
.tcenter{
  text-align: center !important;
}
.tleft{
  text-align: left !important;
}
.tright{
  text-align: right !important;
}

img{
  display: block;
  max-width: 100%;
  height: auto;
}

.btn{
  padding: 0.75em 1.25em;
  border-radius: 0;
  border: none;
  font-weight: 500;
  i{
    margin-left: 10px;
  }
  &.btn-primary{
    background: $brown2;
    color: #fff;
  }
}

.cf:before,
.cf:after {
    content: " "; /* 1 */
    display: table; /* 2 */
}

.cf:after {
    clear: both;
}
.cf {
    *zoom: 1;
}


.c-black{
  color: $black !important;
}


