.tbl{
  display: table;
  vertical-align: middle;
  height: 100%;
  width: 100%;
  margin-bottom: 0;
  border: 0;
}

.tbl > thead > tr > th, 
.tbl > thead > tr > td, 
.tbl > tbody > tr > th, 
.tbl > tbody > tr > td, 
.tbl > tfoot > tr > th, 
.tbl > tfoot > tr > td {
  padding: 8px;
  line-height: 1.42857;
  vertical-align: top;
  border-top: 1px solid #ddd;
}

.tbl-striped > tbody > tr:nth-of-type(odd) {
  background-color: #f9f9f9;
}


.col-xs-5ths,
.col-sm-5ths,
.col-md-5ths,
.col-lg-5ths {
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
}

.col-xs-5ths {
    width: 20%;
    float: left;
}

@media (min-width: 768px) {
    .col-sm-5ths {
        width: 20%;
        float: left;
    }
}

@media (min-width: 992px) {
    .col-md-5ths {
        width: 20%;
        float: left;
    }
}

@media (min-width: 1200px) {
    .col-lg-5ths {
        width: 20%;
        float: left;
    }
}


