@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,500,500i,600,700,900");
@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon.eot");
  src: url("../fonts/icomoon.eot#iefix") format("embedded-opentype"), url("../fonts/icomoon.ttf") format("truetype"), url("../fonts/icomoon.woff") format("woff"), url("../fonts/icomoon.svg#icomoon") format("svg");
  font-weight: normal;
  font-style: normal; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Open Sans", sans-serif;
  background: #FFFBE1;
  font-size: 16px; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Open Sans", sans-serif;
  margin: 0; }

.tcenter {
  text-align: center !important; }

.tleft {
  text-align: left !important; }

.tright {
  text-align: right !important; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.btn {
  padding: 0.75em 1.25em;
  border-radius: 0;
  border: none;
  font-weight: 500; }
  .btn i {
    margin-left: 10px; }
  .btn.btn-primary {
    background: #2D1100;
    color: #fff; }

.cf:before,
.cf:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */ }

.cf:after {
  clear: both; }

.cf {
  *zoom: 1; }

.c-black {
  color: #000 !important; }

