.tbl {
  display: table;
  vertical-align: middle;
  height: 100%;
  width: 100%;
  margin-bottom: 0;
  border: 0; }

.tbl > thead > tr > th,
.tbl > thead > tr > td,
.tbl > tbody > tr > th,
.tbl > tbody > tr > td,
.tbl > tfoot > tr > th,
.tbl > tfoot > tr > td {
  padding: 8px;
  line-height: 1.42857;
  vertical-align: top;
  border-top: 1px solid #ddd; }

.tbl-striped > tbody > tr:nth-of-type(odd) {
  background-color: #f9f9f9; }

.col-xs-5ths,
.col-sm-5ths,
.col-md-5ths,
.col-lg-5ths {
  position: relative;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px; }

.col-xs-5ths {
  width: 20%;
  float: left; }

@media (min-width: 768px) {
  .col-sm-5ths {
    width: 20%;
    float: left; } }

@media (min-width: 992px) {
  .col-md-5ths {
    width: 20%;
    float: left; } }

@media (min-width: 1200px) {
  .col-lg-5ths {
    width: 20%;
    float: left; } }

#header .container {
  padding-top: 2em;
  padding-bottom: 1em; }

#header .logo {
  margin: 0; }
  #header .logo a {
    width: 288px;
    height: 69px;
    display: block;
    background: url("../img/logo.png") no-repeat;
    background-size: contain;
    text-indent: -9999em; }

#header .w-call {
  display: inline-block;
  text-align: center;
  float: right; }
  #header .w-call h4 {
    font-size: 1.563em;
    color: #582100;
    margin: 0.35em 0 0.25em; }
  #header .w-call span {
    display: block;
    font-size: 0.875em; }

#nav {
  position: relative; }

nav {
  padding: 0.5em 0; }
  nav ul {
    margin: 0;
    text-align: center; }
    nav ul li {
      display: inline-block; }
      nav ul li a {
        color: #582200;
        font-size: 1.375em;
        font-weight: 600;
        padding: 0.5em 0;
        margin: 0 1em;
        display: block; }
      nav ul li:hover a {
        color: #2D1100;
        text-decoration: none; }

.featured .slider-slick .slider-item {
  position: relative; }
  .featured .slider-slick .slider-item img {
    width: 100%; }
  .featured .slider-slick .slider-item .text {
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    color: #fff;
    font-family: "Book Antiqua", sans-serif;
    transform: translate(-50%, -50%); }
    .featured .slider-slick .slider-item .text h3 {
      font-size: 2.813em; }
    .featured .slider-slick .slider-item .text h4 {
      font-size: 2.188em; }

#footer {
  background: #2c2c2c;
  padding: 1em 0; }
  #footer .block-list {
    margin: 0 0 1.5em; }
    #footer .block-list .logo {
      width: 120px;
      margin: 0 0 1em; }
    #footer .block-list h3 {
      color: #fff;
      font-size: 16px;
      margin: 0 0 0.5em; }
    #footer .block-list ul li a {
      color: #fff;
      font-size: 12px; }
    #footer .block-list ul.social li {
      display: inline-block; }
      #footer .block-list ul.social li a {
        height: 24px;
        width: 24px;
        display: block;
        border-radius: 100%;
        background: #fff;
        color: #000;
        text-align: center;
        line-height: 24px; }
    #footer .block-list p {
      color: #fff;
      font-size: 12px; }
    #footer .block-list p.copy {
      padding: 0 0 0 1em;
      margin: 0; }
    #footer .block-list .w-form input.text {
      border: 1px solid #fff;
      padding: 0.4em;
      float: left;
      width: 100%;
      max-width: 300px; }
    #footer .block-list .w-form button.btn {
      vertical-align: top;
      padding: 0.49em  1em;
      border-radius: 0; }
      #footer .block-list .w-form button.btn i {
        margin: 0; }

.sidebar.sidebar-filter {
  padding: 0 1em; }
  .sidebar.sidebar-filter h3 {
    margin: 0 0 0.5em;
    padding: 0 0 0.25em;
    font-size: 1.25em;
    text-transform: uppercase;
    border-bottom: 1px solid #fbdbbd;
    position: relative; }
    .sidebar.sidebar-filter h3:after {
      width: 100%;
      height: 1px;
      background: #ff7e00;
      position: absolute;
      left: 0;
      bottom: -2px; }
  .sidebar.sidebar-filter .w-filter {
    margin: 1.5em 0; }
    .sidebar.sidebar-filter .w-filter ul li {
      display: block;
      margin: 0.25em 0; }
      .sidebar.sidebar-filter .w-filter ul li label {
        vertical-align: top;
        position: relative;
        padding: 0 0 0 24px; }
        .sidebar.sidebar-filter .w-filter ul li label input[type=checkbox] {
          display: none; }
          .sidebar.sidebar-filter .w-filter ul li label input[type=checkbox]:checked + .filtros-check {
            background: #ff7e00;
            border: 1px solid #ff7e00; }
        .sidebar.sidebar-filter .w-filter ul li label .filtros-check {
          position: absolute;
          top: 0;
          text-align: center;
          left: 0;
          display: block;
          width: 18px;
          height: 18px;
          border: 1px solid #e1e1e1;
          border-radius: 2px; }
          .sidebar.sidebar-filter .w-filter ul li label .filtros-check i {
            font-size: 10px;
            position: absolute;
            color: #fff;
            top: 2px;
            left: 3px; }
    .sidebar.sidebar-filter .w-filter ul.colors li {
      display: inline-block;
      margin: 0 5px 0 0; }
      .sidebar.sidebar-filter .w-filter ul.colors li:last-child {
        margin: 0; }
      .sidebar.sidebar-filter .w-filter ul.colors li a span {
        display: block;
        width: 20px;
        height: 20px;
        background: #ccc; }
      .sidebar.sidebar-filter .w-filter ul.colors li a.red span {
        background: red; }
      .sidebar.sidebar-filter .w-filter ul.colors li a.azul span {
        background: blue; }
      .sidebar.sidebar-filter .w-filter ul.colors li a.rosado span {
        background: pink; }
      .sidebar.sidebar-filter .w-filter ul.colors li a.verde span {
        background: green; }
      .sidebar.sidebar-filter .w-filter ul.colors li a.amarillo span {
        background: yellow; }

[class^="ads-"], [class*=" ads-"] {
