@media screen and (max-width: 768px) {
  #val-area{
    padding: 0 0 65%;
    &:after{
      max-width: 85vw;
      width: 100%;
      height: 85vh;
    }
  }
  

} 

@media screen and (max-width: 520px) {
  .container{
    max-width: 320px;
    padding: 0 5px;
  }

  #val-area{
    
    .text{
      margin: 0 auto;
      max-width: 80%;
      .title{
        margin: 1em auto 1em;
      }
      h3{
        font-size: 1.25em;
      }
      .w-btn {
        .btn{
          padding: 0.5em 0.5em;
          font-size: 1.05em;
        }
      }
    }

  }

}