#header{
  .container{
    padding-top: 2em;
    padding-bottom: 1em;
  }
  .logo{
    margin: 0;
    a{
      width: 288px;
      height: 69px;
      display: block;
      background: url("../img/logo.png") no-repeat;
      background-size: contain;
      text-indent: -9999em; 
    }    
  }
  .w-call{
    display: inline-block;
    text-align: center;
    float: right;
    h4{
      font-size: 1.563em;
      color: $brown;
      margin: 0.35em 0 0.25em;
    }
    span{
      display: block;
      font-size: 0.875em;
    }
  }
  
 
}

#nav{
  position: relative;
}

nav{  
  padding: 0.5em 0;
  ul {
    margin: 0;
    text-align: center;
    li{
      display: inline-block;
      a{
        color: $brown1;
        font-size: 1.375em;
        font-weight: 600;
        padding: 0.5em 0;
        margin: 0 1em;
        display: block;
      }
      &:hover{
        a{
          color: $brown2;
          text-decoration: none;
          
        }       
      }     
     
    }
  }
}

.featured{
  .slider-slick{
    .slider-item{
      position: relative;
      img{
        width: 100%;
      }
      .text{
        text-align: center;
        position: absolute;
        left: 50%;
        top: 50%;
        color: #fff;
        font-family: $f-booka;
        transform: translate(-50%, -50%);
        h3{
          font-size: 2.813em;
        }
        h4{
          font-size: 2.188em;
        }
      }

    }
  }
}