.section-home{
  padding: 1.5em 0;
  .title-section{
    text-align: center;
    margin-bottom: 1.5em;
    h2{
      color: $brown;
      font-size: 3.125em;      
      font-family: $f-booka;
      font-weight: 700;
    }
  }
}
section#services{
  background: #FFFBE1;
}

.w-btn{
  padding: 0.5em 0;
  text-align: center;
  .btn{
    font-family: $f-booka;
    font-size: 1.875em;
    padding: 0.1em 1em;
    
  }
}

.item-services{  
  margin-bottom: 1em;
  h3.title{
    background: $brown1;
    color: #fff;
    font-size: 1.875em;
    padding: 0.25em;
    text-align: center;
    font-family: $f-booka;
  }
  .w-body{
    background: #F2EBE3;
    padding: 1em;
    .detail{
      text-align: center;
      p{
        font-size: 0.875em;
      }
    }
  }

}

section#welcome{
  background: #EAE6CC url("../img/bg_welcome.jpg") no-repeat center center;
  background-size: contain;
  padding: 3em 0;
  .text{
    text-align: center;
  }
  h2{
    color: $brown;
    font-family: $f-booka;
    font-weight: 700;
    font-size: 3.125em;
    margin-bottom: 0.25em;
  }
  h3{
    color: $brown;
    font-size: 2.500em;
  }
}

.item-obituary{
  margin-bottom: 1em;
  .image{
    margin-bottom: 0.5em;
    text-align: center;
    img{
      display: block;
    }    
  }
  h4.title{
    font-size: 1em;
    min-height: 40px;
  }
}

section#about-us{
  padding: 3em 0 0;
  .text{
    padding: 0 2em;
    p{
      color: $brown1;
      font-size: 1.875em;
      margin-bottom: 1em;
      text-align: center;
    }
  }
  .image{
      text-align: right;
  }
}

section#location{
  background: #FCF7E9;
  .text{
    p{
      color: $brown1;
      font-size: 1.875em;
      line-height: 1.2;
      margin-bottom: 1em;
      &:last-child{
        margin-bottom: 0;
      }
      &.small{
        font-size: 1.250em;
      }
    }
  }
}

